/* =================== My Results Page Styles =================== */

.my-results-container {
  max-width: 1400px;
  margin: 2rem auto;
  padding: 2rem;
  min-height: 100vh;
}

/* Header */
.my-results-header {
  background: rgba(255, 255, 255, 0.98);
  border: 2px solid rgba(255, 255, 255, 0.4);
  border-radius: var(--radius-lg);
  padding: var(--spacing-lg);
  margin-bottom: var(--spacing-xl);
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: var(--shadow-lg);
}

.my-results-header h1 {
  color: var(--primary-color);
  margin: 0;
  font-size: 2.5rem;
  font-weight: 800;
}

.my-results-header p {
  color: #424242;
  margin: 0.5rem 0 0 0;
  font-size: 1.1rem;
}

.my-results-back-btn {
  background: linear-gradient(135deg, var(--primary-color), var(--primary-dark));
  color: white;
  border: none;
  padding: 1rem 2rem;
  border-radius: var(--radius-xl);
  cursor: pointer;
  font-weight: 700;
  transition: all var(--transition);
  box-shadow: 0 4px 15px rgba(25, 118, 210, 0.4);
  text-transform: uppercase;
  letter-spacing: 1px;
}

.my-results-back-btn:hover {
  transform: translateY(-3px);
  box-shadow: 0 8px 25px rgba(25, 118, 210, 0.6);
}

/* Stats Grid */
.my-results-stats-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 2rem;
  margin-bottom: 3rem;
}

.my-results-stat-card {
  background: white;
  border: 3px solid #e3f2fd;
  border-radius: var(--radius-lg);
  text-align: center;
  padding: 2.5rem 2rem;
  box-shadow: var(--shadow-sm);
  transition: all var(--transition);
}

.my-results-stat-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 25px rgba(25, 118, 210, 0.2);
  border-color: var(--primary-color);
}

.my-results-stat-icon {
  font-size: 3.5rem;
  margin-bottom: 1rem;
  animation: float 3s ease-in-out infinite;
}

.my-results-stat-value {
  font-size: 3rem;
  font-weight: 900;
  color: var(--primary-color);
  margin: 0.5rem 0;
}

.my-results-stat-label {
  color: #424242;
  font-size: 1.1rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 1px;
}

/* Section */
.my-results-section {
  background: rgba(255, 255, 255, 0.98);
  padding: 2.5rem;
  border-radius: var(--radius-lg);
  box-shadow: var(--shadow-lg);
  margin-bottom: 2rem;
  border: 2px solid rgba(255, 255, 255, 0.4);
}

.my-results-section h2 {
  color: var(--primary-color);
  margin-bottom: 1.5rem;
  font-size: 2rem;
  font-weight: 800;
}

/* Submissions List */
.my-results-submissions-list {
  display: grid;
  gap: 2rem;
  margin-top: 2rem;
}

.my-results-submission-card {
  background: white;
  border: 3px solid #e3f2fd;
  border-radius: var(--radius-lg);
  padding: 2rem;
  box-shadow: var(--shadow-sm);
  transition: all var(--transition);
}

.my-results-submission-card:hover {
  box-shadow: var(--shadow-md);
  transform: translateY(-3px);
}

.my-results-submission-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 2rem;
  padding-bottom: 1rem;
  border-bottom: 2px solid #e3f2fd;
}

.my-results-submission-header h3 {
  color: var(--primary-color);
  margin: 0;
  font-size: 1.5rem;
  font-weight: 700;
}

.my-results-submission-date {
  color: #757575;
  font-size: 0.9rem;
  margin: 0.5rem 0 0 0;
}

.my-results-status-badge {
  padding: 0.6rem 1.2rem;
  border-radius: 30px;
  font-weight: 700;
  font-size: 0.9rem;
  text-transform: uppercase;
  letter-spacing: 1px;
  color: white;
}

.my-results-status-badge.passed {
  background: linear-gradient(135deg, var(--success-color), #059669);
  box-shadow: 0 4px 15px rgba(16, 185, 129, 0.4);
}

.my-results-status-badge.failed {
  background: linear-gradient(135deg, var(--danger-color), #dc2626);
  box-shadow: 0 4px 15px rgba(239, 68, 68, 0.4);
}

/* Submission Body */
.my-results-submission-body {
  display: grid;
  grid-template-columns: 200px 1fr;
  gap: 2rem;
  align-items: center;
  margin-bottom: 2rem;
}

.my-results-score-display {
  display: flex;
  justify-content: center;
  align-items: center;
}

.my-results-score-circle {
  width: 180px;
  height: 180px;
  position: relative;
}

.my-results-score-circle svg {
  width: 100%;
  height: 100%;
}

.my-results-score-text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}

.my-results-percentage {
  display: block;
  font-size: 2.5rem;
  font-weight: 900;
  color: var(--primary-color);
  line-height: 1;
}

.my-results-score-detail {
  display: block;
  font-size: 1rem;
  color: #757575;
  margin-top: 0.5rem;
}

.my-results-submission-details {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1rem;
}

.my-results-detail-item {
  display: flex;
  align-items: center;
  gap: 0.8rem;
  padding: 1rem;
  background: #f5f5f5;
  border-radius: var(--radius-md);
  border: 2px solid #e0e0e0;
  color: var(--text-primary);
  font-weight: 600;
transition: all var(--transition);
}
.my-results-detail-item:hover {
background: #e3f2fd;
border-color: var(--primary-color);
transform: translateX(5px);
}
.my-results-detail-icon {
font-size: 1.5rem;
}
/* Submission Footer */
.my-results-submission-footer {
margin-top: 2rem;
padding-top: 1.5rem;
border-top: 2px solid #e3f2fd;
}
.my-results-view-details-btn {
width: 100%;
padding: 1rem 2rem;
background: linear-gradient(135deg, var(--primary-color), var(--primary-dark));
color: white;
border: none;
border-radius: var(--radius-xl);
font-weight: 700;
font-size: 1rem;
cursor: pointer;
transition: all var(--transition);
box-shadow: 0 4px 15px rgba(25, 118, 210, 0.4);
text-transform: uppercase;
letter-spacing: 1px;
}
.my-results-view-details-btn:hover {
transform: translateY(-3px);
box-shadow: 0 8px 25px rgba(25, 118, 210, 0.6);
}
/* Empty State */
.my-results-empty-state {
text-align: center;
padding: 4rem 2rem;
background: white;
border: 3px dashed #e0e0e0;
border-radius: var(--radius-lg);
margin-top: 2rem;
}
.my-results-empty-state p {
color: #424242;
font-size: 1.2rem;
margin: 1rem 0;
font-weight: 600;
}
.my-results-empty-icon {
font-size: 4rem;
margin-bottom: 1rem;
}
.my-results-browse-btn {
margin-top: 2rem;
padding: 1rem 2.5rem;
background: linear-gradient(135deg, var(--primary-color), var(--primary-dark));
color: white;
border: none;
border-radius: var(--radius-xl);
font-weight: 700;
font-size: 1rem;
cursor: pointer;
transition: all var(--transition);
box-shadow: 0 4px 15px rgba(25, 118, 210, 0.4);
text-transform: uppercase;
letter-spacing: 1px;
}
.my-results-browse-btn:hover {
transform: translateY(-3px);
box-shadow: 0 8px 25px rgba(25, 118, 210, 0.6);
}
/* Loading State */
.my-results-loading {
text-align: center;
padding: 5rem 2rem;
}
.my-results-loading h3 {
color: white;
margin-top: 1rem;
font-size: 1.3rem;
}
/* Glass Container Override for Loading */
.my-results-glass-container {
background: rgba(255, 255, 255, 0.98);
border: 2px solid rgba(255, 255, 255, 0.4);
box-shadow: var(--shadow-lg);
border-radius: var(--radius-lg);
padding: 3rem 2rem;
text-align: center;
}
/* Responsive Design */
@media (max-width: 1024px) {
.my-results-submission-body {
grid-template-columns: 1fr;
text-align: center;
}
.my-results-score-circle {
margin: 0 auto;
}
}
@media (max-width: 768px) {
.my-results-container {
padding: 1rem;
}
.my-results-header {
flex-direction: column;
gap: 1.5rem;
text-align: center;
padding: 1.5rem;
}
.my-results-header h1 {
font-size: 1.8rem;
}
.my-results-stats-grid {
grid-template-columns: 1fr;
gap: 1rem;
}
.my-results-stat-icon {
font-size: 2.5rem;
}
.my-results-stat-value {
font-size: 2rem;
}
.my-results-section {
padding: 1.5rem;
}
.my-results-submission-card {
padding: 1.5rem;
}
.my-results-submission-header {
flex-direction: column;
gap: 1rem;
text-align: center;
}
.my-results-submission-details {
grid-template-columns: 1fr;
}
.my-results-score-circle {
width: 150px;
height: 150px;
}
.my-results-percentage {
font-size: 2rem;
}
}
@media (max-width: 480px) {
.my-results-header h1 {
font-size: 1.5rem;
}
.my-results-stat-card {
padding: 1.5rem 1rem;
}
.my-results-back-btn,
.my-results-view-details-btn,
.my-results-browse-btn {
padding: 0.9rem 1.5rem;
font-size: 0.9rem;
}
}
.my-results-submissions-list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  gap: 1rem;
  margin-top: 1rem;
}

.my-results-submission-card {
  background: #f9f9f9;
  border: 1px solid #ddd;
  border-radius: 12px;
  padding: 1rem;
  cursor: pointer;
  transition: transform 0.2s, box-shadow 0.2s;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.my-results-submission-card:hover {
  transform: translateY(-4px);
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.1);
}

.my-results-submission-title {
  font-weight: 600;
  font-size: 1.1rem;
  margin-bottom: 0.5rem;
  color: #333;
}

.my-results-submission-info {
  display: flex;
  justify-content: space-between;
  font-size: 0.9rem;
  color: #666;
  margin-bottom: 0.5rem;
}

.my-results-view-btn {
  align-self: flex-end;
  font-size: 0.85rem;
  font-weight: 500;
  color: #007bff;
  text-decoration: underline;
}
